@import url(https://fonts.googleapis.com/css?family=Roboto);
.fastest {
  -webkit-animation-duration: .2s;
          animation-duration: .2s; }

button {
  outline: none; }
  button:focus {
    outline: none; }

h1, p {
  font-family: Roboto;
  font-size: 1rem; }

.splash {
  background-color: #000;
  position: fixed;
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 100; }

.movie-container {
  display: flex;
  overflow-x: scroll;
  -webkit-overflow-scrolling: touch; }

.movie-container::-webkit-scrollbar {
  display: none; }

.movie {
  margin-right: 0.5rem;
  margin-bottom: 1rem; }

.movie-container-header {
  margin-left: 0.5rem; }

#featured {
  width: 100vw; }
  #featured img {
    height: 100%;
    width: 100%;
    object-fit: cover;
    object-position: 100% 80%;
    box-shadow: 0 0 8px 8px #000 inset; }

body {
  background-color: #171717;
  color: #EEE;
  padding: 0;
  margin: 0; }

footer .bottom-nav {
  bottom: 0;
  height: 4rem;
  background-color: #000;
  width: 100vw;
  display: flex;
  justify-content: space-around;
  align-items: center;
  -webkit-transition: 0.2s;
  transition: 0.2s; }
  footer .bottom-nav .bottom-nav-labels {
    color: #FFF;
    display: flex;
    justify-content: space-around;
    align-items: center; }

footer {
  position: fixed;
  bottom: 0;
  height: 4rem;
  background-color: #000;
  width: 100vw;
  display: flex;
  justify-content: space-around; }
  footer div {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center; }
    footer div span {
      font-size: 0.8rem; }

.bottom-nav-labels {
  color: #FFF;
  display: flex;
  justify-content: space-around;
  align-items: center; }

html, body {
  max-width: 100%;
  overflow-x: hidden; }

img {
  height: 25vh; }

.details-poster-container {
  display: flex;
  justify-content: center;
  margin-top: 2rem; }
  .details-poster-container img {
    box-shadow: 1px 10px 14px -5px rgba(0, 0, 0, 0.53);
    height: 35vh; }

.details-background-blur {
  width: 100vw; }
  .details-background-blur img {
    height: 100%;
    width: 100%;
    object-fit: cover;
    object-position: 100% 80%;
    position: absolute;
    -webkit-filter: blur(50px);
    /* Safari 6.0 - 9.0 */
    filter: blur(50px);
    opacity: 0.4;
    z-index: -1; }

.details-info-paragraph {
  display: flex;
  justify-content: space-evenly;
  margin-top: 1rem; }

.see-season-now {
  display: flex;
  justify-content: space-evenly;
  margin-top: 1rem;
  color: #FFF;
  font-weight: bold; }

.movie-details-desc {
  font-weight: 100; }

.details-cast {
  color: #7F7F7F;
  font-size: 0.8rem; }

.t-green {
  color: #59ff2f;
  font-weight: bold; }

.t-grey {
  color: #7F7F7F; }

.red-play {
  background-color: red;
  border: none;
  width: 100%;
  padding: 0.3rem;
  color: #FFF;
  font-weight: bold; }

.close-button-container {
  margin-bottom: 30px; }

.close-button {
  position: absolute;
  top: 1rem;
  right: 1rem; }

.menu-container {
  height: 92vh;
  background-color: #272727; }
  .menu-container .profile-container > * {
    flex: 4 4 30%; }
  .menu-container .profile-container {
    position: fixed;
    height: 12rem;
    background-color: #000;
    top: 0;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap; }
    .menu-container .profile-container .profile {
      display: flex;
      flex-direction: column;
      align-items: center; }
    .menu-container .profile-container .edit-profile {
      text-align: center;
      color: #7F7F7F; }
      .menu-container .profile-container .edit-profile div svg {
        margin: 0 0.5rem; }
    .menu-container .profile-container img {
      width: 3rem;
      height: 3rem;
      margin: 1rem 1rem 0.25rem 1rem; }
    .menu-container .profile-container svg {
      margin: 1rem 1rem 0.25rem 1rem; }
    .menu-container .profile-container span {
      font-size: 0.8rem;
      color: #7F7F7F; }
      .menu-container .profile-container span.active-user {
        color: #FFF; }
  .menu-container .menu-items-container {
    font-size: 1rem;
    padding-top: 12rem; }
    .menu-container .menu-items-container .menu-item-more {
      display: block;
      border-bottom: 0.2rem solid #000;
      padding: 0.5rem; }
      .menu-container .menu-items-container .menu-item-more span {
        margin-left: 1rem; }
    .menu-container .menu-items-container .menu-item {
      display: block;
      padding: 0.5rem; }
      .menu-container .menu-items-container .menu-item.version {
        color: #7F7F7F; }
      .menu-container .menu-items-container .menu-item.version {
        min-height: 100%; }

.search-bar-page-wrapper .search-banner {
  margin-top: 0.25rem;
  background-color: #444;
  color: #999;
  padding: 0.25rem 0.75rem;
  font-weight: bold;
  font-size: 0.8rem; }

.search-bar-page-wrapper .movie-container-search {
  display: flex;
  flex-wrap: wrap; }

.search-bar-page-wrapper .search-bar-top-container {
  -webkit-transition: 0.5s;
