.downloads-container {
  display: flex;
  flex-direction: column;
  align-items: center; }
  .downloads-container svg {
    margin-top: 2rem; }
  .downloads-container svg, .downloads-container p, .downloads-container button {
    margin-bottom: 1.5rem; }
  .downloads-container button {
    border: 1px solid #7F7F7F;
    background-color: transparent;
    color: #EEE;
    padding: 0.8rem; }

